import React, { Component } from "react";
import { Input, Row, Col, Button } from 'antd';
import Icon from "../icon";
import './style.less'

class View extends Component {
  handleChange = (index, name, e) => {
    let data = Array.from(this.props.value)
    if (data[index]) {
      data[index][name] = e.currentTarget.value
    }
    this.onChange(data)
  }

  onChange = data => {
    const { onChange } = this.props
    if (onChange) onChange(data)
  };

  add = () => {
    let data = Array.from(this.props.value || [])
    data.push({ key: '', value: '' })
    this.onChange(data)
  }

  remove = index => {
    let data = Array.from(this.props.value || [])
    data.splice(index, 1)
    this.onChange(data)
  }

  render() {
    const { value = [], keyTitle = '键名', valueTitle = '值', btnText = '添加项' } = this.props
    return (
      <div className="key-value-list">
        {
          value.map((item, index) => (
            <Row key={index} style={{ marginBottom: 5 }}>
              <Col flex="auto">
                <Row gutter={[8, 8]}>
                  <Col span={12}><Input placeholder={keyTitle} value={item.key} onChange={this.handleChange.bind(this, index, 'key')} /></Col>
                  <Col span={12}><Input placeholder={valueTitle} value={item.value} onChange={this.handleChange.bind(this, index, 'value')} /></Col>
                </Row>
              </Col>
              <Col flex="30px">
                <Button type="text" shape="circle" danger
                  icon={<Icon iconName={'CalculatorMultiply'} />}
                  onClick={this.remove.bind(this, index)}
                />
              </Col>
            </Row>
          ))
        }
        <Button type="dashed" onClick={this.add} style={{ width: '100%' }}
          icon={<Icon iconName={'Add'} marginRight={5} color="#007eff" />}
        >
          {btnText}
        </Button>
      </div>
    );
  }
}
export default View